    },
    computed: {
        type: function () {
            let c1 = this.value1 ? 'E' : 'I'
            let c2 = this.value2 ? 'N' : 'S'
            let c3 = this.value3 ? 'T' : 'F'
            let c4 = this.value4 ? 'J' : 'P'
            return c1 + c2 + c3 + c4
        }
    }
  };
